.slider {
  max-width: 90%;
  position: relative;
  margin-left: 15px;
  height: 500px;

  .slider__item {
    animation-name: fade;
    animation-duration: 1.5s;

    img {
      object-fit: cover;
      width: 100%;
      height: 500px;
    }
  }

  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }
}

.slider .previous,
.slider .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 16px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
}

.slider .previous:hover,
.slider .next:hover {
  background-color: rgb(0 0 0 / 20%);
}
